import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AboutbuttonComponent } from './aboutbutton/aboutbutton.component';
import { ContactbuttonComponent } from './contactbutton/contactbutton.component';
import { HomebuttonComponent } from './homebutton/homebutton.component';
import { ProjectbuttonComponent } from './projectbutton/projectbutton.component';
import { ResumebuttonComponent } from './resumebutton/resumebutton.component';

const routes: Routes = [
  {path : '', redirectTo: '/home', pathMatch: 'full'},
  {path : 'home', component : HomebuttonComponent},
  {path : 'about', component : AboutbuttonComponent},
  {path : 'project', component : ProjectbuttonComponent},
  {path : 'resume', component : ResumebuttonComponent},
  {path : 'contact', component : ContactbuttonComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
